<template>
  <div class="register">
    <div class="content">
      <h1>用户注册</h1>
    <el-form :model="signUpForm" :label-position="labelPosition" class="bac" label-width="80px" ref="signUpFormRef" :rules="signUpFromRules">
      <!--用户名-->
      <el-popover
          placement="right"
          title="用户名"
          :width="100"
          trigger="hover"
          content="英文字母或数字组成的4-15位字符，可以包含下划线"
      >
        <template #reference>
          <el-form-item label="用户名" class="fromItem" prop="username">
            <el-input v-model="signUpForm.username" clearable></el-input>
          </el-form-item>
        </template>
      </el-popover>

      <el-popover
          placement="right"
          title="密码"
          :width="100"
          trigger="hover"
          content="由6-20位英文字母或数字组成至少有一个大写字母"
      >
        <template #reference>
          <el-form-item label="密码" class="fromItem" prop="password">
            <el-input v-model="signUpForm.password"
                      type="password"
                      prefix-icon="el-icon-key"
                      show-password clearable></el-input>
          </el-form-item>
        </template>
      </el-popover>

      <!--确认密码-->
      <el-form-item label="确认密码" class="fromItem" prop="checkPass">
        <el-input v-model="signUpForm.checkPass"
                  type="password"
                  prefix-icon="el-icon-key"
                  show-password clearable></el-input>
      </el-form-item>
      <el-checkbox-group v-model="checkList" min="2" max="4">
        <el-checkbox label="小说" ></el-checkbox>
        <el-checkbox label="文学"></el-checkbox>
        <el-checkbox label="散文"></el-checkbox>
        <el-checkbox label="名著"></el-checkbox>
        <el-checkbox label="心理学"></el-checkbox>
        <el-checkbox label="哲学"></el-checkbox>
        <el-checkbox label="经济学"></el-checkbox>
        <el-checkbox label="社会学"></el-checkbox>
        <el-checkbox label="政治学"></el-checkbox>
        <el-checkbox label="国学"></el-checkbox>
        <el-checkbox label="文化"></el-checkbox>
        <el-checkbox label="历史"></el-checkbox>
        <el-checkbox label="思想"></el-checkbox>
        <el-checkbox label="人文"></el-checkbox>
        <el-checkbox label="科技"></el-checkbox>
        <el-checkbox label="艺术"></el-checkbox>
        <el-checkbox label="流行"></el-checkbox>
        <el-checkbox label="音乐"></el-checkbox>
        <el-checkbox label="美术"></el-checkbox>
        <el-checkbox label="科普"></el-checkbox>
        <el-checkbox label="军事"></el-checkbox>
        <el-checkbox label="国学"></el-checkbox>
        <el-checkbox label="专记"></el-checkbox>
        <el-checkbox label="近代史"></el-checkbox>
        <el-checkbox label="回忆录"></el-checkbox>
        <el-checkbox label="武侠"></el-checkbox>
        <el-checkbox label="科幻"></el-checkbox>
        <el-checkbox label="悬疑"></el-checkbox>
        <el-checkbox label="奇幻"></el-checkbox>
        <el-checkbox label="推理"></el-checkbox>
        <el-checkbox label="青春"></el-checkbox>
        <el-checkbox label="爱情"></el-checkbox>
        <el-checkbox label="成长"></el-checkbox>
        <el-checkbox label="生活"></el-checkbox>
        <el-checkbox label="女性"></el-checkbox>
        <el-checkbox label="教育"></el-checkbox>
        <el-checkbox label="美食"></el-checkbox>
        <el-checkbox label="健康"></el-checkbox>
        <el-checkbox label="理财"></el-checkbox>
      </el-checkbox-group>
      <el-button @click="handleSubmit" class="zc_button">注册</el-button>
    </el-form>
    </div>
  </div>
</template>
<script>

import { register1,register2 } from "@/api/user";
export default {
  name: "register",
  data() {
    const checkUsername = (rule, value, callback) => {
      if (!/^[a-zA-Z0-9_]{4,16}$/.test(value)) {
        this.checkUsername = false
        callback(new Error('请正确输入用户名'))
      }else{
        this.checkUsername = true
        callback();
      }
    }

    const checkPassword = (rule, value, callback) => {
      if (!/^(?=.*[A-Z])[A-Za-z\d]{6,20}$/.test(value)) {
        this.checkPassword = false
        callback(new Error('6-20位英文字母或数字且至少有一个大写字母'))
      }else{
        this.checkPassword = true
        callback();
      }
    }

    const checkPassword2 = (rule, value, callback) => {
      if (value !== this.signUpForm.password) {
        this.checkPassword2 = false
        callback(new Error('两次输入密码不一致!'));
      } else {
        this.checkPassword2 = true
        callback();
      }
    }
    return {
      labelPosition: 'right',
      signUpForm:{
        username: '',
        password: '',
        checkPass:'',
      },

      checkList:['小说','文学'],
      signUpFromRules: {
        username: [
          {required: true, message: "请输入用户名", trigger: "blur"},
          {validator: checkUsername, trigger: "blur"},
          {min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur'},

        ],
        password: [
          {required: true, message: "请输入密码", trigger: "blur"},
          {validator: checkPassword, trigger: "blur"}
        ],
        checkPass:[
          {required: true, message: "请再次输入密码", trigger: "blur"},
          { validator: checkPassword2, trigger: 'blur' }
        ],
      },
      checkUsername:false,
      checkPassword:false,
      checkPassword2:false,
    };

  },
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    updateCancel() {
      this.visible = false;
    },
    handleSubmit() {
      if (this.checkUsername&&this.checkPassword&&this.checkPassword2){
        this.sub()
      }
    },
    sub(){
      register1({ username: this.signUpForm.username, password: this.signUpForm.password, genres: this.checkList.toString()}).then(
          (res) => {
            if (res === true) {
              this.$message.info(`${this.signUpForm.username}注册成功`);
              this.$router.push('/login');
            } else {
              this.$message.info("注册失败，用户名已被占用");
            }
          }
      );
    },
  },
};
</script>

<style scoped>
.register{
  width: 1600px;
  /*text-align: center;*/
  height: 800px;
  position: absolute;
  right: 8%;
  /*top: 0;*/
  background-image: url("../../images/bac1.png");
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 500px;
}

.content {
  position: relative;
  background-color: #ffffff;
  margin-top: 50px;
  margin-left: 250px;
  width: 850px;
  height: 550px;
  padding-right: 30px;
  padding-left: 50px;
  padding-top: 5px;
}

h1 {
  text-align: center;
  height: 46px;
  line-height: 46px;
  font-size: 24px;
  color: #1890ff;
  font-weight: 700;
  margin-bottom: 30px;
  margin-top: 20px;
}

.zc_button {
  position: absolute;
  bottom: 80px;
  left: 43%;
}

</style>