<template>
  <div class="nav w">
    <a-row class="position1">
      <a-col :xs="12" :sm="12" :md="6" :lg="8" :xl="10" :order="1">
        <div class="logo">
          <router-link to="/home">
           <el-icon class="el-icon-s-management"></el-icon>&emsp;图书推荐系统
          </router-link>
        </div>
      </a-col>
      <a-col :xs="2" :sm="2" :md="6" :lg="10" :xl="10" :order="2">
        <a-menu mode="horizontal" class="nav_box">
          <a-menu-item>
            <router-link to="/Movie">
              <a-icon type="edit" />图书
            </router-link>
          </a-menu-item>
          <a-menu-item>
            <router-link to="/archives">
              <a-icon type="tags" />归档
            </router-link>
          </a-menu-item>

          <a-menu-item v-if="!login">
            <router-link to="/login">
              <a-icon type="login" />登录
            </router-link>
          </a-menu-item>
          <a-menu-item v-if="!login">
            <router-link to="/register">
              <a-icon type="user-add" />注册
            </router-link>
          </a-menu-item>

          <a-menu-item v-if="login">
            <router-link to="/user">
              <a-icon type="like" />个人信息
            </router-link>
          </a-menu-item>
          <a-menu-item v-if="login">
            <a @click="Logout">
              <a-icon type="close-circle" />退出
            </a>
          </a-menu-item>
        </a-menu>
      </a-col>
    </a-row>
  </div>
</template>
<script>
import login from "../user/login";
import register from "../user/register";
export default {
  components: {
    login,
    register,
  },
  data() {
    return {
      current: ["mail"],
      login: false,

    };
  },
  mounted(){
    this.Judge()
  },
  methods: {
    Judge(){
      if (window.sessionStorage.getItem('username')){
        this.login = true
      }
    },
    Logout() {
      this.login = false;
      window.sessionStorage.removeItem("username");
      window.sessionStorage.removeItem("password");
      this.$message.info("成功退出登录")
      this.$router.push("/home");
    },

    updateCancel() {
      this.visible = false;
    },
  },
};
</script>

