import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

const routes = [
  {  path: '/',
    name: 'Home',
    component: () => import( '../views/home/Home.vue'),
    redirect: "/Home",
    meta: {
      title: "首页"
    }
  }, {
    path: '/Home',
    name: 'Home',
    component: () => import('../views/home/Home.vue'),
  },{
      path: '/login',
      name: 'login',
      component: () => import('../views/user/login.vue'),
    }, {
      path: '/register',
      name: 'register',
      component: () => import('../views/user/register.vue'),
  },{
    path: '/Movie',
    name: 'Movie',
    component: () => import('../views/movie/Movie.vue'),
    children:[{
        path: '/movie/BookDetail',
        name: 'BookDetail',
        component: () => import('../views/movie/BookDetail.vue'),
      }]
  }, {
    path: '/archives',
    name: 'Archives',
    component: () => import('../views/archives/Archives.vue')
  }, {
    path: '/user',
    name: 'user',
    meta: {
      requireAuth: true,
      title: 'Change'
    },
    component: () => import('../views/about/user.vue')
  }, {
    path: '/404',
    name: '404',
    component: () => import('../views/err/404.vue')
  }]

const router = new VueRouter({
  routes
})

export default router
